#contactSection {
    .social-media-contact {
        display: flex;
        justify-content: center;

        @media (max-width:991px) {
            flex-wrap: wrap;
            align-items: center;
        }

        li {
            list-style: none;
            @media (max-width:991px) {
                margin: 10px;
            }

            a {
                margin: 0px 15px;
                text-decoration: none;
                background: $white;
                transition: transform 0.5s;
                display: flex;
                width: 100px;
                height: 100px;
                align-items: center;
                justify-content: center;
                border: 1px solid rgba(0, 124, 196, 0);
                box-shadow: 6px 6px 10px -1px rgba(0,124,196,0.15), 6px -6px 10px -1px rgba(255,255,255,0.7);

                &:hover {
                    box-shadow:inset 4px 4px 6px -2px rgba(0,0,0,0.2), inset -4px -4px 6px -1px rgba(255,255,255,0.7),-0.5px -0.5px 0px -1px rgba(255,255,255,1),0.5px 0.5px 0px rgba(0,0,0,0.15),0px 12px 10px -10px rgba(0,0,0,0.05);
                    border: 1px solid rgba(0, 124, 196, 0.1);
                    transform: translateY(2px);

                    svg {
                        transform: scale(0.90);
                    }
                }
               
                &.facebook {
                    &:hover {
                        svg{
                            fill: $facebook;
                        }
                    }
                }
                &.instagram {
                    &:hover {
                        svg{
                            fill: $instagram;
                        }
                    }
                }
                &.whatsapp {
                    &:hover {
                        svg{
                            fill: $whatsapp;
                        }
                    }
                }
                &.twitter {
                    &:hover {
                        svg{
                            fill: $twitter;
                        }
                    }
                }
                &.linkedin {
                    &:hover {
                        svg{
                            fill: $linkedin;
                        }
                    }
                }
                &.github {
                    &:hover {
                        svg{
                            fill: $primary-color;
                        }
                    }
                }
                &.gmail {
                    &:hover {
                        svg{
                            fill: $gmail;
                        }
                    }
                }
            }

            svg {
                transition: transform 0.5s;
            }
        }
    }
}

  